.btn {
  font-family: var(--font-family);
  display: block;
  border: 0;
  padding: 10px 30px;
  border-radius: 50px;
  min-width: 180px;
  font-size: 16px;
  line-height: 1.2;
  color: var(--light-color);
  transition: text-shadow 0.2s ease-in;
  cursor: pointer;
  font-weight: 400;
  overflow: hidden;
  position: relative;
  z-index: 5;
}
.btn::after, .btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.3s ease-in-out;
  z-index: -1;
}
.btn:hover::after {
  opacity: 1;
}
@media (max-width: 960px) {
  .btn {
    padding: 10px 20px;
    min-width: 170px;
  }
}
@media (max-width: 425px) {
  .btn {
    font-size: 14px;
    min-width: 160px;
  }
}

@media (max-width: 960px) {
  .header__btn {
    margin-left: auto;
    margin-right: 10px;
  }
}

.btn--primary::before {
  background: linear-gradient(94.26deg, #c89afc 9.51%, #7c6afa 90.23%);
}
.btn--primary::after {
  background: linear-gradient(270deg, #c89afc 9.51%, #7c6afa 90.23%);
  opacity: 0;
}

.btn--secondary {
  font-weight: 700;
}
.btn--secondary::before {
  background: linear-gradient(94.78deg, #df5950 11.19%, #451046 93.72%);
}
.btn--secondary::after {
  background: linear-gradient(270deg, #df5950 11.19%, #451046 93.72%);
  opacity: 0;
}